"use client";

import { useScroll } from "@/hooks/use-scroll";
import { Logo } from "@/components/logo";
import { cn } from "@/components/lib/utils";
import { MobileNav } from "@/components/mobile-nav";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { useQuiz } from "@/context/quiz-context";

export const navLinks = [
  { label: "Services", href: "/services" },
  { label: "Insights", href: "/blog" },
  { label: "About", href: "/about" },
];

export function Header() {
  const scrolled = useScroll(10);
  const pathname = usePathname();
  const { openQuiz } = useQuiz();

  // Detect if we are on a blog post page (e.g., /blog1, /blog2, etc.)
  const isBlogPost = pathname.match(/^\/blog\d+$/);
  const isDarkTheme = !isBlogPost;

  return (
    <header
      className={cn(
        "sticky top-0 z-50 mx-auto w-full max-w-full transition-all duration-300",
        isDarkTheme ? {
          "bg-dark-bg/90 backdrop-blur-xl border-b border-white/10 shadow-2xl shadow-blue-500/10": scrolled,
          "bg-dark-bg/50 backdrop-blur-sm border-b border-white/5": !scrolled
        } : {
          "bg-white/95 backdrop-blur-xl border-b border-gray-200 shadow-lg shadow-black/5": scrolled,
          "bg-transparent": !scrolled
        }
      )}
    >
      <nav className="flex items-center justify-between px-6 h-16 max-w-6xl mx-auto">
        {/* Logo */}
        <Link className="flex items-center gap-2 group" href="/">
          <Logo
            variant={isDarkTheme ? "light" : "dark"}
            className="transition-transform duration-300 group-hover:scale-105"
          />
        </Link>

        {/* Desktop Nav */}
        <div className="hidden md:flex items-center gap-8">
          {navLinks.map((link, i) => {
            const isActive = pathname === link.href || pathname.startsWith(link.href + '/');
            return (
              <Link
                key={i}
                href={link.href}
                className={cn(
                  "text-sm font-medium transition-all duration-300 relative group",
                  isDarkTheme
                    ? (isActive ? "text-white" : "text-gray-400 hover:text-white")
                    : (isActive ? "text-blue-600" : "text-gray-600 hover:text-blue-600")
                )}
              >
                {link.label}
                <span className={cn(
                  "absolute -bottom-1 left-0 h-0.5 bg-gradient-to-r from-blue-500 to-indigo-500 transition-all duration-300",
                  isActive ? "w-full" : "w-0 group-hover:w-full"
                )}></span>
              </Link>
            );
          })}

          <button
            onClick={openQuiz}
            className={cn(
              "px-6 py-2.5 font-semibold rounded-full transition-all duration-300 text-sm shadow-lg hover:scale-105",
              isDarkTheme
                ? "bg-gradient-to-r from-blue-600 to-indigo-600 text-white shadow-blue-500/30 hover:shadow-blue-500/40"
                : "bg-blue-600 text-white hover:bg-blue-700 shadow-blue-600/20"
            )}
          >
            Get Your Roadmap
          </button>
        </div>

        <MobileNav isDarkTheme={isDarkTheme} />
      </nav>
    </header>
  );
}
